<template>
  <div class="chart">
    <div id="box">

    </div>
    <div class="title">
      {{$t('Container_CPUusage')}}
<!--      CPU-->

    </div>
  </div>
</template>

<script>
export default {
  props: ['cpudata', 'datatime'],
  watch: {
    // cpudata
    cpudata: function () {
      this.charts()
    }
  },
  mounted () {
    // echarts
    this.charts()
  },
  methods: {
    charts () {
      // dom
      let myChart = this.$echarts.init(document.getElementById('box'))
      // 
      let option = {
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          data: ['CPU']
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: this.datatime
        },
        yAxis: {
          type: 'value',
          axisLabel: {
            formatter: '{value}%'
          }
        },
        series: [{
          name: 'CPU',
          data: this.cpudata,
          type: 'line',
          color: ['#dd6b66', '#759aa0', '#e69d87', '#8dc1a9', '#ea7e53', '#eedd78', '#73a373', '#73b9bc', '#7289ab', '#91ca8c', '#f49f42'],
          smooth: true
        }]
      }
      // 
      myChart.setOption(option)
    }
  }
}
</script>

<style lang="scss" scoped>
.chart{
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 230px;
}
#box{
  width: 100%;
  height: 200px;
}
.title{
  height: 30px;
  width: 100%;
  background: #067FB5;
  color: #fff;
  text-align: center;
  line-height: 30px;
}
</style>
