<template>
  <div class="chart">
    <div id="box1">

    </div>
    <div class="title">
      {{$t('Container_Memoryusage')}}
<!--      -->
    </div>
  </div>
</template>

<script>
export default {
  props: ['ramdata', 'datatime'],
  watch: {
    // 
    ramdata: function () {
      this.charts()
    }
  },
  mounted () {
    this.charts()
  },
  methods: {
    charts () {
      let myChart = this.$echarts.init(document.getElementById('box1'))
      // 
      let option = {
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          data: ['Memory', 'Cache']
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: this.datatime
        },
        yAxis: {
          type: 'value',
          axisLabel: {
            formatter: '{value}MB'
          }
        },
        series: [
          {
            name: 'Memory',
            type: 'line',
            stack: '',
            data: this.ramdata.active_anon
          },
          {
            name: 'Cache',
            type: 'line',
            stack: '',
            data: this.ramdata.cache
          }
        ]
      }
      // 
      myChart.setOption(option)
    }
  }
}
</script>

