.chart{
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 230px;
}
#box1{
  width: 100%;
  height: 200px;
}
.title{
  height: 30px;
  width: 100%;
  background: #067FB5;
  color: #fff;
  text-align: center;
  line-height: 30px;
}
